@import url(//cdn.rawgit.com/rtaibah/dubai-font-cdn/master/dubai-font.css);
*,*::before,*::after{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%;font-family:"Dubai-Bold", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif}body{height:100%;width:100%;background-color:#f6f6f6}.home{padding:8rem;display:flex;flex-direction:column;align-items:center;height:100vh}.home h1{font-size:7rem;margin-bottom:8rem}.home ul{list-style:none}.home ul a{display:flex;justify-content:center;margin-bottom:6rem;padding:3rem 8rem;text-decoration:none;font-size:3rem;color:white}.category-1{box-shadow:0px 10px 40px -10px rgba(0,64,128,0.2);transition:all 0.2s;border-radius:10rem;background-image:linear-gradient(90.5deg, #fa9cf5 1.6%, #b64ce9 98.2%)}.category-1:hover{transform:scale(1.03);box-shadow:0px 10px 40px 1px rgba(0,64,128,0.2)}.category-2{box-shadow:0px 10px 40px -10px rgba(0,64,128,0.2);transition:all 0.2s;border-radius:10rem;background-image:linear-gradient(109.5deg, #4cddf2 11.2%, #5c79ff 91.1%)}.category-2:hover{transform:scale(1.03);box-shadow:0px 10px 40px 1px rgba(0,64,128,0.2)}.category-3{box-shadow:0px 10px 40px -10px rgba(0,64,128,0.2);border-radius:10rem;transition:all 0.2s;background-color:#fad961;background-image:linear-gradient(90deg, #fad961 0%, #f76b1c 100%)}.category-3:hover{transform:scale(1.03);box-shadow:0px 10px 40px 1px rgba(0,64,128,0.2)}.newIdeas{height:100vh;padding:10rem 5rem;display:flex;flex-direction:column;align-items:center}.newIdeas h1{font-size:6rem;margin-bottom:8rem}.newIdeas ul{display:flex;justify-content:space-evenly;list-style:none;width:80%}.newIdeas ul a{background-image:linear-gradient(120deg, #969696 0%, #6b6b6b 100%);text-align:center;text-decoration:none;font-size:2.7rem;width:40rem;height:12rem;display:flex;justify-content:center;align-items:center;text-decoration:none;color:white;border-radius:2rem;opacity:40%;transition:box-shadow 0.2s}.newIdeas ul a:hover{opacity:100%;background-image:linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);box-shadow:0px 10px 40px -10px rgba(0,64,128,0.2)}.deck{display:grid;grid-template-columns:repeat(auto-fit, minmax(30rem, 1fr));grid-gap:2rem;justify-items:center}.card{text-decoration:none;color:black;transition:all 0.2s;display:flex;flex-direction:column;justify-content:space-around;align-items:center;background-color:white;height:40rem;width:30.5rem;border-radius:1.5rem;box-shadow:5px 12px 20px rgba(59,59,59,0.048);cursor:pointer}.card-journey{background-image:linear-gradient(to top, #00c6fb 0%, #005bea 100%);cursor:auto}.card-img{width:13rem;display:flex;align-items:center;flex:3 1}.card-img img{width:100%}.card-name{flex:1 1;font-size:1.7rem}.card:hover{background-image:linear-gradient(to top, #00c6fb 0%, #005bea 100%);box-shadow:5px 12px 20px rgba(59,59,59,0.281);transform:scale(1.03)}.search{margin-bottom:3rem;width:100%;display:flex;flex-direction:row-reverse;justify-content:space-between}.search_input{background-color:#dadada;box-shadow:5px 12px 20px rgba(59,59,59,0.096);padding:1.5rem 3rem;border:none;font-family:inherit;font-size:2rem;border-radius:1rem;width:100%}.search_input:focus{outline:none}.search_button{cursor:pointer;transition:all 0.2s;font-size:2rem;font-family:inherit;border:none;width:20rem;margin-right:2rem;border-radius:1rem;box-shadow:5px 12px 20px rgba(59,59,59,0.096);background-image:linear-gradient(-225deg, #dfffcd 0%, #90f9c4 48%, #39f3bb 100%)}.search_button:hover{transform:scale(1.03);box-shadow:5px 12px 20px rgba(59,59,59,0.137)}.journey{padding:3rem;width:100%;display:flex;flex-direction:column;align-items:center}.journey-progress{margin-bottom:3rem;font-size:2.5rem}.journey-space{display:flex;flex-direction:column;align-items:center;padding:2rem}.journey-space--question{margin-top:3rem;text-align:center}.journey-space--question h1{font-size:3rem}.journey-space--question-answer{margin-top:1rem}.journey-space--question-answer textarea{background-color:#ffffff;padding:1rem 2rem;resize:none;border:0.1rem solid #d8d8d8;border-radius:1rem;box-shadow:5px 12px 20px rgba(59,59,59,0.048);font-family:inherit;font-size:1.7rem}.journey-space--btns{margin-top:1rem;width:44.5rem;display:flex;flex-direction:row-reverse;justify-content:space-between;border:none}.journey-space--btns-btn{box-shadow:0px 5px 10px rgba(59,59,59,0.089);cursor:pointer;border:none;padding:1rem 2rem;font-family:inherit;border-radius:0.5rem;transition:all 0.2s}.journey-space--btns-btn:hover{transform:scale(1.1);box-shadow:0px 5px 20px rgba(59,59,59,0.151)}.next{background-image:linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%)}.skip{background-image:linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%)}.finish{background-image:linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%)}.result{padding:3rem;text-align:center}.result h1{font-size:4rem;margin-bottom:2rem}.result-card{width:40%;margin:auto;display:flex;justify-content:space-around;margin-bottom:4rem}.result-ideas{display:grid;grid-template-columns:repeat(auto-fit, minmax(35rem, 1fr));grid-gap:2rem;margin-bottom:4rem;justify-items:center}.result-ideas--idea{background-image:linear-gradient(-60deg, #ff5858 0%, #f09819 100%);border-radius:0.5rem;padding:3rem 5rem;font-size:2rem;width:35rem;display:flex;justify-content:center;align-items:center;box-shadow:0px 5px 10px rgba(59,59,59,0.089)}.result-btn{padding:3rem 5rem;border:none;border-radius:0.5rem;font-size:2.2rem;font-family:inherit;box-shadow:0px 5px 10px rgba(59,59,59,0.089);background-image:linear-gradient(to top, #0ba360 0%, #3cba92 100%);transition:all 0.2s;color:white;cursor:pointer}.result-btn:hover{transform:scale(1.03);box-shadow:0px 5px 20px rgba(59,59,59,0.151)}.match{padding:8rem;display:flex;flex-direction:column;align-items:center}.match-cards{border-radius:1rem;width:66.6rem;display:flex;align-items:center;justify-content:space-around}.match-space{margin-top:4rem}.match-space--title{font-size:3rem;text-align:center;margin-bottom:2rem}

